@import url('https://fonts.googleapis.com/css2?family=Inter&family=Noto+Sans+JP:wght@400;500&family=Noto+Sans+Kannada&display=swap');

.material-symbols-outlined {
  cursor: pointer;
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}

.message-send-btn {
  background-color: #5850EC;
  border-radius: 20px;
  height: 40px;
  width: 40px;
  border: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 10px 12px 5px rgba(0, 0, 0, 0.1);
}

.message-input {
  display: flex;
  justify-content: space-between;
  width: 95%;
  height: 15%;
  margin: 2%;
}

.message {
  border-radius: 5px;
  padding: 8px;
  margin: 4px;
  max-width: 85%;
}

.message-container {
  display: flex;
  margin-bottom: 10px;
}

.assistant .message-container {
  justify-content: flex-end;
}

.user .message-container {
  justify-content: flex-start;
}

.message-container.user {
  align-self: flex-start;
  max-width: 60%;
}

.message-container.assistant {
  align-self: flex-end;
  width: 60%;
}

.chat-messages {
  display: flex;
  flex-direction: column;
  max-height: 300px;
  padding: 10px;
  overflow-y: auto;
  height: 90%;
  max-width: 95%;
}

/* Media queries for different screen sizes */
@media screen and (max-width: 2560px) {
  .chat-messages {
    max-height: 300px; 
  }
}

@media screen and (max-width: 2000) {
  .chat-messages {
    max-height: 270px; 
  }
}

@media screen and (max-width: 1600px) {
  .chat-messages {
    max-height: 240px;
  }
}

@media screen and (max-width: 1550px) {
  .chat-messages {
    max-height: 230px;
  }
}

@media screen and (max-width: 1440px) {
  .chat-messages {
    max-height: 200px; 
  }
}

@media screen and (max-width: 1024px) {
  .chat-messages {
    max-height: 180px; 
  }
}

.chat-main {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 85%;
  background-color: white;
  border-radius: 0 0 15px 15px;
  font-size: 14px;
}

.chat-buttons > button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 5px;
  margin: 2px;
  font-weight: 600;
}

.chat-buttons {
  display: flex;
  justify-content: center;
  width: 30%;
  margin-right: 5px;
}

.chat-header {
  background-color: #5850EC;
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 15%;
  width: 100%;
  border-radius: 15px 15px 0 0;
}

.message-body {
  font-family: 'Inter', sans-serif;
  position: fixed;
  bottom: 10px;
  right: 10px;
  height: 350px;
  width: 320px;
  background-color: transparent;
  box-shadow: 0px 10px 20px 5px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  z-index: 10;
}

.Chat-input {
  width: 80%;
  padding: 2%;
  border-radius: 15px;
  border: 1px solid #4B5865;
  background-color: #F9FAFB;
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.1);
}

.Chat-input:focus {
  outline: 1px solid #5850EC;
}

.user {
  border-radius: 10px 10px 10px 0;
  background-color: #939ADF;
  color: white;
}

.assistant {
  border-radius: 10px 10px 0 10px;
  background-color: #5850EC;
  color: white;
}

/* fallback */
@font-face {
  font-family: 'Material Symbols Outlined';
  font-style: normal;
  font-weight: 100 700;
  src: url(https://fonts.gstatic.com/s/materialsymbolsoutlined/v154/kJEhBvYX7BgnkSrUwT8OhrdQw4oELdPIeeII9v6oFsI.woff2) format('woff2');
}

.material-symbols-outlined {
  font-family: 'Material Symbols Outlined';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}